import React from "react";
import { ManifestElementListText } from "../../dev/src/slide.types";

export type ListProps = {
  options: ManifestElementListText;
};

export const List = ({ options }: ListProps) => {
  return (
    <ul>
      {options.value.map((item, idx) => {
        return <li key={idx}>{item}</li>;
      })}
    </ul>
  );
};

export default {
  List,
};
