import React from "react";
import * as styles from "./styles.module.scss";
import { ManifestElements } from "../dev/src/slide.types";
import { Title, Subtitle, LineDivider, List } from "./components";

export interface BulletPointsProps {
  manifest: ManifestElements;
}

export const BulletPoints = ({ manifest }: BulletPointsProps) => {
  const titleConfig = manifest.title;
  const subtitleConfig = manifest.subtitle;
  const listConfig = manifest.list;
  console.log("manifest", manifest);
  if (titleConfig.type !== "text") {
    console.error("Title value not a string!");
    return <></>;
  }

  if (subtitleConfig.type !== "text") {
    console.error("Subtitle value not a string!");
    return <></>;
  }

  if (listConfig.type !== "listText") {
    console.error("List value must be an array of strings!");
    return <></>;
  }

  return (
    <div className={styles.bulletPoints}>
      <Title options={titleConfig} />
      <LineDivider />
      <Subtitle options={subtitleConfig} />
      <List options={listConfig}></List>
    </div>
  );
};

export default {
  BulletPoints,
};
